import { mapState } from 'vuex'
import tkiQrcode from "../../components/tki-qrcode"
export default { 
    components: {
        tkiQrcode
    },
    data () {
        return {
            currentBannerIndex: 0,
            myCardArr: [],
            QRStr: '',
            currentTime: ''
        }
    },
    onShow() {
        this.getMyCard()
        this.creatQrcode()
    },
    watch: {
        currentTime() {                  
            this.changeQR()
        }
    },
    computed: {
        ...mapState(['vCardBaseInfo','userInfo'])
    },
    methods: {
        async getMyCard() {
            const res = await this.$api.myCardList()
            if (res.code === 0) {
                let myCardArr = res.data.data
                if (myCardArr.length === 0) {                                     
                    this.$tip.alertDialog(
                        '还没有权益卡，快去加入吧',
                        '去购买').then(() => {
                            uni.navigateTo({
                                url: `/pages/buyPage/buyPage?id=${this.vCardBaseInfo.id}`
                            });
                        })
                    return
                }
                this.myCardArr = myCardArr
                this.creatQrcode()
            }
        },  
        getQRBack (res) {

        },    
        handleBannerChange (e) {
            this.currentBannerIndex = e.detail.current
        },           
        creatQrcode() {
            let temp = new Date()
            this.currentTime = new Date().getTime()
            this.$nextTick(() => {                    
                this.$refs.qrcode._makeCode()
            })
        },      
        changeQR () {   
            if (this.QRStr) {
                this.$refs.qrcode._clearCode()         
            }
            let code = 111
            let obj = {
                code
            }
            this.QRStr = JSON.stringify(obj)
        },
    },
}
